@media (min-width:320px)  {
  .fade-in-section {
    opacity: 0;
    -webkit-transform: translateY(20vh);
        -ms-transform: translateY(20vh);
            transform: translateY(20vh);
    visibility: hidden;
    -webkit-transition: opacity 0.6s ease-out, -webkit-transform 1.2s ease-out;
    transition: opacity 0.6s ease-out, -webkit-transform 1.2s ease-out;
    -o-transition: opacity 0.6s ease-out, transform 1.2s ease-out;
    transition: opacity 0.6s ease-out, transform 1.2s ease-out;
    transition: opacity 0.6s ease-out, transform 1.2s ease-out, -webkit-transform 1.2s ease-out;
    will-change: opacity, visibility;
  }

  .is-visible {
    opacity: 1;
    -webkit-transform: none;
        -ms-transform: none;
            transform: none;
    visibility: visible;
  }

  .fadeInText {
    font-size: 16px;
    line-height: 1.8;
    -webkit-hyphens: auto;
        -ms-hyphens: auto;
            hyphens: auto; 
    text-align: justify;
    margin-bottom: 2em;
  }

  .fadeInText:nth-child(3n+3) {
    margin-left: 1em;
  } 

  .fadeInText:nth-child(3n+4) {
    margin-right: 1em;
  } 
}
@media (min-width:961px)  {
  .fadeInText {
    font-size: 24px;
    line-height: 1.7;
  }

  .fadeInText:nth-child(3n+3) {
    margin-left: 3em;
  } 

  .fadeInText:nth-child(3n+4) {
    margin-right: 3em;
  } 
}


