    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    opacity: 0.1;
    font-size: 200px;
  }
  
  @-webkit-keyframes fadeOutDecrease {
    from {
      opacity: 1;
      font-size: 300px;
    }
    to {
      opacity: 0.1;
      font-size: 200px;
    }
  }
  
  @keyframes fadeOutDecrease {
    from {
      opacity: 1;
      font-size: 300px;
    }
    to {
      opacity: 0.1;
      font-size: 200px;
    }
  }
}

@media (min-width:961px)  {
  .finished {
    -webkit-animation-name: fadeOutDecrease;
            animation-name: fadeOutDecrease;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    opacity: 0.1;
    font-size: 300px;
  }
  
  @-webkit-keyframes fadeOutDecrease {
    from {
      opacity: 1;
      font-size: 500px;
    }
