@import url('https://fonts.googleapis.com/css2?family=Lora&family=Rajdhani:wght@400;600&display=swap');


@media (min-width:320px)  {
  .start-modal-main {
    position:fixed;
    z-index: 100;
    color: black;
    background: white;
    -webkit-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    overflow: scroll;
    width: 100%;
    height: 100%;
    top: 50%; 
    left: 50%;
  }

  .start-modal-content {
    padding: 4em 2em;
    text-align: center;
    line-height: 2;
  }

  .start-modal-title, .start-modal-text {
    font-family: 'Lora', sans-serif;
  }

  .start-confirmationButton {
    color: white;
    background-color: black;
    margin: 0.5em 0.5em 0.5em 0;
    padding: 8px 16px;
    border: white 1px solid;
    border-radius: 5px;
    font-size: 20px;
    font-family: 'Rajdhani', sans-serif;
    text-decoration: none;
    -webkit-animation-name: fadeIn;
            animation-name: fadeIn;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    cursor: pointer;
  }
}

@media (min-width:768px)  {
  .start-modal-main {
    width: 50%;
    max-width: 1000px;
    height: auto;
    border-radius: 3px;
  }

  .start-modal-text {
    font-size: 20px;
  }
}



